import { ValidatorFn } from '@angular/forms';
export class InputBase<T> {
  label: string;
  key: string;
  value: T;
  classes: string[];
  group: number;
  group_classes: string[];
  validators: ValidatorFn[];
  controlType: string;

  onChange?: Function;

  accept?: string;
  allowMaxSize?: number;
  apiUpload?: string;
  uploadPath?: string;
  multiple?: string;

  // custom-dropdown - dropdown - ListCheckBox - Radio
  options?: { label: string; value: string }[];

  // dropzone
  message?: string;
  upload_path?: string;
  acceptedFiles?: string;
  maxFilesize?: number;
  maxFiles?: number;
  showPreview?: boolean;
  url?: string;
  headers?: object;
  paramName?: string;
  resultTransformer?: Function;

  // text-box + tinymce
  type?: string;
  rows?: number;

  // mask-text
  mask?: {}[];
  valueWithCharacter?: boolean;
  guide?: boolean;
  placeholder?: string;

  // tinymce
  height?: string;
  plugins?: string;

  // datepicker
  showIcon?: Boolean;
  monthNavigator?: Boolean;
  yearNavigator?: Boolean;
  yearRange?: string;
  showTime?: Boolean;
  timeOnly?: Boolean;
  dateFormat?: string;

  // select2
  tags?: Boolean;

  constructor(
    options: {
      label?: string;
      key?: string;
      value?: T;
      classes?: string[];
      group?: number;
      group_classes?: string[];
      validators?: ValidatorFn[];
      controlType?: string;
    } = {}
  ) {
    this.label = options.label || '';
    this.key = options.key || '';
    this.value = options.value;
    this.classes = options.classes || [];
    this.group = options.group === undefined ? 1 : options.group;
    this.group_classes = options.group_classes || ['col-12'];
    this.validators = Array.isArray(options.validators) ? options.validators : [];
    this.controlType = options.controlType || '';
  }
}
