import { Component, OnInit, Input, forwardRef, ElementRef, Renderer, AfterViewInit } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import * as _ from 'lodash';
declare const $: any;

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'app-custom-select2',
  templateUrl: './custom-select2.component.html',
  styleUrls: ['./custom-select2.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => CustomSelect2Component),
      multi: true
    }
  ]
})
export class CustomSelect2Component implements AfterViewInit, ControlValueAccessor {
  @Input()
  tags: Boolean;
  @Input()
  placeholder: string;
  @Input()
  options: any;
  @Input()
  _value: any[];

  defaultValue = [];

  private disabled: boolean;
  private onChange: Function;
  private onTouched: Function;

  constructor(private element: ElementRef, private renderer: Renderer) {
    this.onChange = (val: any) => {};
    this.onTouched = () => {};
    this.disabled = false;
  }
  writeValue(val: any): void {
    if (val) {
      this.value = val;
    }
  }

  get value() {
    return this._value;
  }

  set value(val) {
    this._value = val;
    this.onChange(this._value);
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  ngAfterViewInit() {
    // tslint:disable-next-line:prefer-const
    let $this = this;

    // tslint:disable-next-line:prefer-const
    let options: any = {};
    if (this.tags !== undefined && this.tags != null) {
      options.tags = this.tags;
    }
    if (this.placeholder !== undefined && this.placeholder != null) {
      options.placeholder = this.placeholder;
    }
    $('#select2-reactive-form').select2(options);

    // tslint:disable-next-line:prefer-const
    let oldData = $('#select2-reactive-form').select2('data');
    if (oldData != null && oldData !== undefined && !_.isEmpty(oldData)) {
      _.forEach(oldData, function(value) {
        $this.defaultValue.push(value.id);
      });
      this.value = this.defaultValue;
    }
    // console.log('Af-value', this.value);

    $('#select2-reactive-form').change(function() {
      // tslint:disable-next-line:prefer-const
      let value = $(this).val();
      $this.value = value;
      // console.log('Ch-value', $this.value);
    });
  }
}
